const IndexPage = () => {
  return (
    <main style={{ backgroundColor: "#27272a", color: "d4d4d8" }}>
      <Navbar>Navbar</Navbar>
      {/* <StaticImage alt="Clifford, a reddish-brown pitbull, posing on a couch and looking stoically at the camera" */}
        {/* src="https://pbs.twimg.com/media/E1oMV3QVgAIr1NT?format=jpg&name=large" /> */}
        <div className={styles.container}>
          <main className={markdownStyles.markdownBody}>
            <div className={markdownStyles.pre}>
              <img src="resources/images/IMG_7014_crop.png" alt="Myself :D" width="250" height="250" align="right" style={{padding: "5px", marginLeft: "20px", borderRadius: "10px"}} />
              <p class="text-4xl font-bold">Jian-Kai Zhu</p>
              <p class="text-justify">
              I'm currently a research assistant in <a href='https://www.iis.sinica.edu.tw/en/index.html'>Institute of Information Science</a>, <a href='https://www.sinica.edu.tw/en'>Academia Sinica</a>, working under the guidance of <a href='https://sites.google.com/site/jenchunlin/'>Dr. Jen-Chun Lin</a>. I received my M.S. degree at <a href='https://www.ntu.edu.tw/english/'>National Taiwan University</a> under the supervision of <a href='https://www.csie.ntu.edu.tw/~cyy/'>Prof. Yung-Yu Chuang</a>. I'm also a typical computer graphics enjoyer.
              </p>
              <p class="text-justify">
              I'm currently submitting a text-to-motion work in SIGGRAPH ASIA 2025, <a href="./anonymous/" target="_blank" rel="noopener noreferrer">here</a> is some of our additional results in our submitted paper.
              </p>
              <p class="text-justify">
              Currently, I'm looking for Ph.D opportunities.
              My research interests cover:
              <ul>
                <li>Computer Vision</li>
                <li>Computer Graphics</li>
                <li>Motion synthesis and understanding</li>
                <li>Machine Learning</li>
              </ul>
              And I'm open to any topics that align my research interests.<br></br>
              Learn more about me from: <a href="mailto:jack111331@gmail.com">E-mail</a> | <Link to='/resources/docs/CV.pdf'>Resume</Link>
              </p>

              <p class="text-justify">
              Aside from that, my personal webpage contains some of my ongoing side projects, including <a href="./ongoing_render/">Cel-shading testbed (Requires a little time to load resources)</a> and <a href="./blog/">blog pages</a> to record my experiences on doing side projects.
              </p>

              <h2>Publications</h2>
              <div className="publication grid" style={{gridTemplateColumns: "1fr 3fr", gridAutoRows: "1fr", gridColumnGap: "5px"}}>
                <div className="">
                  <img src="resources/publication/stylefaceuv/thumb.png" alt="StyleFaceUV" width="360" height="360" align="left" style={{padding: "5px", borderRadius: "10px"}} />
                </div>
                <div className="pl-3">
                  <a href="/BlogPage/resources/publication/stylefaceuv/StyleFaceUV_main.pdf"><b>StyleFaceUV: a 3D Face UV Map Generator for View-Consistent Face Image Synthesis</b></a>
                  <p className="mb-0">Wei-Chieh Chung*, Jian-Kai Zhu*, I-Chao Shen, Yu-Ting Wu, Yung-Yu Chuang (*: joint first authors)<br></br>
                    British Machine Vision Conference (BMVC) 2022<br></br>
                    [<a href="/BlogPage/resources/publication/stylefaceuv/StyleFaceUV_main.pdf">Paper</a>][<a href="https://github.com/jack111331/StyleFaceUV">Code</a>][<a href="/BlogPage/resources/publication/stylefaceuv/StyleFaceUV_supp.pdf">Supplementary materials</a>]</p>
                </div>
              </div>
            </div>
          </main>

        </div>
    </main>
  )
}

export default IndexPage

export const Head = () => <Seo title="Jian-Kai Zhu" />